angular.module("app").factory("MemberService", ['$q', '$http', function($q, $http) {
    return {
        get: function(memberId) {
            return $http.get('/api/v1/members/' + memberId);
        },
        post: function(member) {
            return $http.post('/api/v1/members/' + member.id, member);
        }
    };
}]);
